<template>
    <div class="downloads">
        <vue-headful :title="$t('downloads.meta-title')" :description="$t('downloads.meta-description')"/>

        <section class="hero is-download">
            <div class="hero-body">
                <div class="container">
                    <h2 class="title is-size-3 has-text-centered is-unselectable">{{ $t('downloads.latest') }}</h2>
                    <p class="subtitle has-text-centered is-unselectable">
                        <span v-if="!isLoadingLatest">
                            {{ !isEmpty($store.release) ? $t('home.version').replace('{0}', $store.release.version) : '...' }}
                        </span>

                        <b-skeleton v-if="isLoadingLatest" height="20px" width="180px" animated></b-skeleton>
                    </p>

                    <div class="columns is-centered is-vcentered is-multiline is-mobile">
                        <div class="column is-narrow has-text-centered">
                            <!-- <b-tooltip :label="$t('home.installer-info')" type="is-light" position="is-top" animated multilined> -->
                                <b-button ref="installerButton" type="is-primary" size="is-large"
                                    :style="{ 'min-width': getMinWidthPortable() }" :loading="isLoadingLatest" tag="a" :target="downloads.length > 0 ? '_self' : '_blank'" 
                                    :href="$store.release.download_link_instx64" 
                                    :inverted="!isLoadingLatest" :outlined="!isLoadingLatest" @click="$gtag.event('Download', {'event_category': 'Clicks', 'event_label': 'Installerx64'})">
                                    {{ $t('home.installerX64') }}
                                </b-button>
                            <!-- </b-tooltip> -->
                            
                            <b-skeleton v-if="isLoadingLatest" height="20px" width="180px" animated></b-skeleton>
                        </div>

                        <div class="column is-narrow has-text-centered">
                            <b-tooltip :label="$t('home.installerX86-info')" type="is-light" position="is-top" animated multilined>
                                <b-button ref="installerButton" type="is-primary" size="is-large"
                                    :style="{ 'min-width': getMinWidthPortable() }" :loading="isLoadingLatest" tag="a" :target="downloads.length > 0 ? '_self' : '_blank'" 
                                    :href="$store.release.download_link_instx86" 
                                    :inverted="!isLoadingLatest" :outlined="!isLoadingLatest" @click="$gtag.event('Download', {'event_category': 'Clicks', 'event_label': 'Installerx86'})">
                                    {{ $t('home.installerX86') }}
                                </b-button>
                            </b-tooltip>
                            
                            <b-skeleton v-if="isLoadingLatest" height="20px" width="180px" animated></b-skeleton>
                        </div>

                        <div class="column is-narrow has-text-centered">
                            <!-- <b-tooltip :label="$t('home.portablex64-info')" type="is-light" position="is-top" animated multilined> -->
                                <b-button ref="portableButton" type="is-primary" size="is-large"
                                    :style="{ 'min-width': getMinWidthInstaller() }" :loading="isLoadingLatest" tag="a" :target="downloads.length > 0 ? '_self' : '_blank'"
                                    :href="$store.release.download_link_portx64" 
                                    :inverted="!isLoadingLatest" :outlined="!isLoadingLatest" @click="$gtag.event('Download', {'event_category': 'Clicks', 'event_label': 'PortableX64'})">
                                     {{ $t('home.portablex64') }}
                                </b-button>
                            <!-- </b-tooltip> -->

                            <b-skeleton v-if="isLoadingLatest" height="20px" width="180px" animated></b-skeleton>
                        </div>

                        <div class="column is-narrow has-text-centered">
                            <b-tooltip :label="$t('home.portablex86-info')" type="is-light" position="is-top" animated multilined>
                                <b-button ref="portableButton" type="is-primary" size="is-large"
                                    :style="{ 'min-width': getMinWidthInstaller() }" :loading="isLoadingLatest" tag="a" :target="downloads.length > 0 ? '_self' : '_blank'"
                                    :href="$store.release.download_link_portx86" 
                                    :inverted="!isLoadingLatest" :outlined="!isLoadingLatest" @click="$gtag.event('Download', {'event_category': 'Clicks', 'event_label': 'PortableX86'})">
                                     {{ $t('home.portablex86') }}
                                </b-button>
                            </b-tooltip>

                            <b-skeleton v-if="isLoadingLatest" height="20px" width="180px" animated></b-skeleton>
                        </div>
                    </div>

                    <b-collapse v-if="!isLoadingLatest && !isEmpty($store.release) && !isEmpty($store.release.description)" class="has-text-light has-text-centered" 
                                :open.sync="isExpanderOpen" position="is-top" animation="slide" aria-id="expander"
                                @open="$gtag.event('Latest release details', {'event_category': 'Clicks', 'event_label': 'Open'})"
                                @close="$gtag.event('Latest release details', {'event_category': 'Clicks', 'event_label': 'Close'})">
                                
                        <a slot="trigger" slot-scope="props" aria-controls="expander" class="has-text-light is-unselectable">
                            <b-icon pack="unicon" :icon="!props.open ? 'uil-angle-down' : 'uil-angle-up'"></b-icon>

                            {{ !props.open ? $t('downloads.show-release-notes') : $t('downloads.hide-release-notes') }}
                        </a>

                        <div class="columns is-centered">
                            <div class="column is-10-tablet is-9-desktop is-8-widescreen is-7-fullhd">
                                <p class="content has-text-light has-text-justified is-unselectable">
                                    <VueShowdown :markdown="!isEmpty($store.release) ? $store.release.description : ''" tag="span"></VueShowdown> 
                                </p>
                            </div>
                        </div>
                    </b-collapse>

                    <p class="has-text-centered">
                        <b-skeleton v-if="isLoadingLatest" height="20px" width="180px" animated></b-skeleton>
                    </p>
                </div>
            </div>
        </section>

        <section class="hero">
            <div class="hero-body">
                <div class="container">
                    <h2 class="title is-size-3 has-text-centered is-unselectable">{{ $t('downloads.releases.title') }}</h2>

                    <b-table ref="table" :data="$store.releaseList" :loading="isLoading" hoverable detailed detail-key="version" 
                        selectable @select="toggle" paginated :per-page="perPage" :current-page.sync="currentPage" :scrollable="false"
                        aria-next-label="Next page" aria-previous-label="Previous page" aria-page-label="Page" aria-current-label="Current page" 
                        :default-sort-direction="defaultSortOrder" :default-sort="[sortField, sortOrder]"
                        @details-open="(row, index) => detailsOpen(row)" @details-close="(row, index) => detailsClose(row)">
                    
                        <b-table-column class="is-unselectable" cell-class="has-pointer-cursor" field="version" :label="$t('downloads.releases.table.version')" :custom-sort="sortVersion" sortable v-slot="props">
                            <span class="tag" :class="versionType(props.row.is_prerelease)">
                                {{ props.row.version }}
                            </span>
                        </b-table-column>

                        <b-table-column class="is-unselectable" cell-class="has-pointer-cursor" field="release_date" :label="$t('downloads.releases.table.date')" sortable centered v-slot="props">
                            {{props.row.release_date ? new Date(props.row.release_date).toLocaleDateString($i18n.locale) : "unknown"}}
                        </b-table-column>

                        <b-table-column class="is-unselectable" cell-class="has-pointer-cursor" field="active_days" :label="$t('downloads.releases.table.days')" sortable numeric>
                            <template v-slot:header="{ column }">
                                <b-tooltip :label="$t('downloads.releases.table.days-info')" type="is-info" size="is-small" animated dashed multilined>
                                    {{ column.label }}
                                </b-tooltip>
                            </template>

                            <template v-slot="props">
                                {{ toLocaleFixed(props.row.active_days, 1, $i18n.locale) }}
                            </template>
                        </b-table-column>

                        <template slot="empty">
                            <section v-if="!isLoading" class="section">
                                <div class="content has-text-grey has-text-centered">
                                    <!-- <p> -->
                                        <b-icon icon="frown" size="is-size-3"></b-icon>
                                    <!-- </p> -->

                                    <p>{{ $t('downloads.releases.table.not-possible') }}</p>
                                </div>
                            </section>

                        </template>

                        <template slot="detail" slot-scope="props">
                            <article class="media">
                                <div class="media-content">
                                    <nav class="level is-marginless">
                                        <div class="level-left">
                                            <a class="level-item" :href="props.row.url" target="_blank" rel="noopener"
                                                @click="$gtag.event('Release links', {'event_category': 'Clicks', 'event_label': 'Release ' + props.row.version })">
                                                <span class="is-size-4 has-text-weight-semibold is-marginless">Coodesker {{ props.row.version }}</span>  
                                            </a>

                                            <p class="level-item is-vcentered">
                                                <small v-html="$t('navigation.download.by').replace('{0}', '<a href={0} target=_blank rel=noopener>@{1}</a>')
                                                    .replace('{0}', props.row.author_url).replace('{1}', props.row.author_login)"
                                                    @click="$gtag.event('Release links', {event_category: 'Clicks', event_label: 'Author ' + props.row.version })"/>
                                            </p>
                                        </div>

                                        <div class="level-right">
                                            <p class="level-item">
                                                <small>{{ since(props.row.release_date_obj, new Date()) }}</small>
                                            </p>
                                        </div>
                                    </nav>

                                    <div class="content has-side-padding">
                                        <VueShowdown :markdown="props.row.description" tag="span"/>
                                    </div>

                                    <hr>

                                    <nav class="level is-marginless is-paddingless">
                                        <div class="level-left"></div>

                                        <div class="level-right">
                                            <div class="level-item has-text-centered">
                                                <div>
                                                    <b-button type="is-info" size="is-medium" tag="a" :href="props.row.download_link_instx64" rel="noopener"
                                                        @click="$gtag.event('Download-Row', {'event_category': 'Clicks', 'event_label': 'Installer ' + props.row.version })">
                                                        {{ $t('downloads.installerx64') }}
                                                    </b-button>
                                                   
                                                </div>
                                            </div>
                                            <div class="level-item has-text-centered">
                                                <div>
                                                    <b-button type="is-info" size="is-medium" tag="a" :href="props.row.download_link_instx86" rel="noopener"
                                                        @click="$gtag.event('Download-Row', {'event_category': 'Clicks', 'event_label': 'Installer ' + props.row.version })">
                                                        {{ $t('downloads.installerx86') }}
                                                    </b-button>
                                                  
                                                </div>
                                            </div>
                                            <div class="level-item has-text-centered">
                                                <div>
                                                    <b-button type="is-info" size="is-medium" tag="a" :href="props.row.download_link_portx64" rel="noopener"
                                                        @click="$gtag.event('Download-Row', {'event_category': 'Clicks', 'event_label': 'Portablex64 ' + props.row.version })">
                                                        {{ $t('downloads.downloadx64') }}
                                                    </b-button>

                                                </div>
                                            </div>
                                            <div class="level-item has-text-centered">
                                                <div>
                                                    <b-button type="is-info" size="is-medium" tag="a" :href="props.row.download_link_portx86" rel="noopener"
                                                        @click="$gtag.event('Download-Row', {'event_category': 'Clicks', 'event_label': 'Portablex86 ' + props.row.version })">
                                                        {{ $t('downloads.downloadx86') }}
                                                    </b-button>

                                                </div>
                                            </div>
                                        </div>
                                    </nav>
                                </div>
                            </article>
                        </template>

                    </b-table>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    import ResponsiveImage from "@/components/ResponsiveImage.vue";
    import helpers from '../mixins/helpers';

    export default {
        components: {
            ResponsiveImage
        },
        mixins: [
            helpers,
        ],

        data() {
            return {
                downloads: [],
                aux: {},
                isLoading: true,
                isLoadingLatest: true,
                isExpanderOpen: false,
                totalDays: 0,
                sortField: "version",
                sortOrder: "desc",
                defaultSortOrder: "desc",
                currentPage: 1,
                perPage: 10
            };
        },

        mounted() {
            this.load();
        },

        methods: {
            getMinWidthInstaller() {
                var size = this.$refs.portableButton !== undefined ? (this.$refs.portableButton.$el.clientWidth + 2) + "px" : 0;

                this.$nextTick().then(() => {
                    this.$refs.installerButton.$el.style.minWidth = 0 + "px";

                    this.$nextTick().then(() => {
                        this.$refs.installerButton.$el.style.minWidth = (this.$refs.portableButton.$el.clientWidth + 2) + "px";
                    });
                });

                return size;
            },
            getMinWidthPortable() {
                var size = this.$refs.installerButton !== undefined ? (this.$refs.installerButton.$el.clientWidth + 2) + "px" : 0;

                this.$nextTick().then(() => {
                    this.$refs.portableButton.$el.style.minWidth = 0 + "px";

                    this.$nextTick().then(() => {
                        this.$refs.portableButton.$el.style.minWidth = (this.$refs.installerButton.$el.clientWidth + 2) + "px";
                    });
                });

                return size;
            },

            sortVersion(a, b, isAsc){
                return this.compareVersion(a.version, b.version) * (isAsc ? 1 : -1);
            },
            compareVersion(versionA, versionB){
                var versionsA = versionA.substr(1).split(/\./g), versionsB = versionB.substr(1).split(/\./g);

                while (versionsA.length || versionsB.length) {
                    var a = Number(versionsA.shift()), b = Number(versionsB.shift());
                    
                    if (a == b)
                        continue;
                    
                    return (a > b || isNaN(b)) ? 1 : -1;
                }
                
                return -1;
            },
            load() {
                if (this.$store.release.length > 0 && this.$store.previousLatestDate && new Date().getTime() - this.$store.previousLatestDate > 300) { //300s, 5 minutes.
                    this.isLoadingLatest = false;
                } else {
                    this.fallbackLoadLatest();
                }

                if (this.$store.releaseList.length > 0 && this.$store.previousDate && new Date().getTime() - this.$store.previousDate > 300) { //300s, 5 minutes.
                    this.isLoading = false;
                } else {
                    this.fallbackLoad();
                }
            },
            fallbackLoad(){
                this.$http.get(`https://api.github.com/repos/coodesker/coodesker-desktop/releases?per_page=70`)
                    .then(async res => {
                        this.treatDataFallback(await res.json());
                    }, error => {
                        console.log("Exception in getting releases, in fallback.", error);

                        this.setDefaultReleaseList();

                        this.$gtag.exception({'description': error, 'fatal': true});
                    })
                    .catch(e => {
                        console.log("Exception in getting releases, in fallback.", e);

                        this.setDefaultReleaseList();

                        this.$gtag.exception({'description': e, 'fatal': true});
                    });
            },
            treatDataFallback(data) {
                this.totalDownloads = 0;
                this.totalDays = 0;
                var previousUpdatedAt = new Date();
                this.downloads = [];

                data.forEach((e, index) => {
                    if (e.assets.length === 0) 
                        return; //Equivalent to continue;

                    var portx64 = e.assets[0];
                    var portx86 = e.assets[1];
                    var instx64 = e.assets[2];
                    var instx86 = e.assets[3];
                    var updatedAt = new Date(e.published_at);

                    //Displayed on the row.
                    this.aux = {};
                    this.aux.version = e.tag_name;
                    this.aux.release_date = updatedAt;
                    this.aux.release_date_obj = updatedAt;
                    this.aux.is_prerelease = e.prerelease;
                    this.aux.active_days = this.dateDiff(previousUpdatedAt, updatedAt);

                    //Displayed on the detailed view.
                    this.aux.author_login = e.author.login;
                    this.aux.author_picture = e.author.avatar_url;
                    this.aux.is_picture_loaded = false;
                    this.aux.author_url = e.author.html_url;
                    this.aux.url = e.html_url;
                    this.aux.description = e.body;

                    this.aux.download_link_portx64 = (portx64 != undefined ? portx64.browser_download_url : undefined);
                    this.aux.download_link_portx86 = (portx86 != undefined ? portx86.browser_download_url : undefined);
                    this.aux.download_link_instx64 = (instx64 != undefined ? instx64.browser_download_url : undefined)
                    this.aux.download_link_instx86 = (instx86 != undefined ? instx86.browser_download_url : undefined)

                    this.downloads.push(this.aux);

                    this.totalDays += this.aux.active_days;
                    previousUpdatedAt = updatedAt;
                });

                this.$store.releaseList = this.downloads;
                // this.$store.release = this.downloads[0];
                this.$store.previousDate = new Date().getTime() / 1000; //Seconds.
                this.$store.totalDays = this.totalDays;
                this.isLoading = false;
            },
            setDefaultReleaseList() {
                this.downloads = [];
                this.totalDownloads = 0;
                this.totalDays = 0;

                this.isLoading = false;
            },
            fallbackLoadLatest() {
                this.$http.get(`https://www.coodesker.com/coodesker-desktop/release.json`)
                    .then(async res => {
                        this.treatDataLatestFallback(await res.json());
                    }, error => {
                        console.log("Exception in getting releases, in fallback.", error);

                        this.setDefaultReleaseLatest();

                        this.$gtag.exception({'description': error, 'fatal': true});
                    })
                    .catch(e => {
                        console.log("Exception in getting releases, in fallback.", e);

                        this.setDefaultReleaseLatest();

                        this.$gtag.exception({'description': e, 'fatal': true});
                    });
            },
            treatDataLatestFallback(data) {
                this.$store.release.download_link_portx64 = data.Portablex64.download;
                this.$store.release.download_link_portx86 = data.Portablex86.download;
                this.$store.release.download_link_instx64 = data.x64.download;
                this.$store.release.download_link_instx86 = data.x86.download;
                this.$store.release.version = data.version; 
                this.$store.release.description = data.description; 

                this.$store.previousLatestDate = new Date().getTime() / 1000; //Seconds.
                this.isLoadingLatest = false;
            },
            setDefaultReleaseLatest() {
                var release = "https://github.com/coodesker/coodesker-desktop/releases";
                this.$store.release.download_link_portx64 = release;
                this.$store.release.download_link_portx86 = release;
                this.$store.release.download_link_instx64 = release;
                this.$store.release.download_link_instx86 = release;
                this.$store.release.version = ""; 
                this.$store.release.description = "";

                this.isLoadingLatest = false;
            },
            toggle(row) {
                this.$refs.table.toggleDetails(row);
            },
            detailsOpen(row) {
                this.$gtag.event('Version details', {'event_category': 'Clicks', 'event_label': 'Expand row ' + row.version});
            },
            detailsClose(row) {
                this.$gtag.event('Version details', {'event_category': 'Clicks', 'event_label': 'Collapse row ' + row.version});
            },
            versionType(prerelease) {
                return prerelease ? "is-warning" : "is-info";
            },
            showError(){
                this.$buefy.toast.open({
                    duration: 5000,
                    message: `It was not possible to load the releases.`,
                    position: 'is-bottom',
                    type: 'is-danger'
                })
            },
            daysInMonth(date) {
                return new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
            }
        },

        computed: {
            projectAge() {
                var end = new Date(new Date().setHours(0, 0, 0, 0));
                var start = new Date(2013, 10 - 1, 12);

                var years = end.getFullYear() - start.getFullYear();
                var months = end.getMonth() - start.getMonth();
                var days = end.getDate() - start.getDate();

                //Work out the difference in months.
                months += years * 12;

                if (days < 0)
                    months -= 1;
                
                //Now add those months to the date of birth.
                start.setMonth(start.getMonth() + months);
                
                //Calculate the difference in milliseconds.
                var diff = end - start;
                
                //Divide that by 86400 to get the number of days.
                var days = Math.round(diff / 86400 / 1000);
                
                //Now convert months back to years and months.
                years = parseInt(months / 12);
                months -= (years * 12);

                //Format age as "xx years, yy months, zz days".
                var text = "";

                if (years)
                    text = this.$t('downloads.releases.dates.year' + (years == 1 ? '' : 's')).replace('{0}', years);
                
                if (months) {
                    if (text.length)
                        text = text + ", ";
                    
                    text += this.$t('downloads.releases.dates.month' + (months == 1 ? '' : 's')).replace('{0}', months);
                }

                if (days) {
                    if (text.length)
                        text = text + ", ";
                    
                    text += this.$t('downloads.releases.dates.day' + (days == 1 ? '' : 's')).replace('{0}', days);
                }
                               
                return text;
            },
            averagePerMonth() {
                var end = new Date(new Date().setHours(0, 0, 0, 0));
                var start = new Date(2013, 10 - 1, 12);

                var years = end.getFullYear() - start.getFullYear();
                var months = end.getMonth() - start.getMonth();
                var days = end.getDate() - start.getDate();

                //Work out the difference in months.
                months += years * 12;
                months += days / this.daysInMonth(end);

                //if (days < 0)
                //    months -= 1; 

                return (this.downloads.length > 0 ? this.downloads.length : this.$store.releaseList.length) / months;
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "@/mixins/colors.scss";

    //Background of the hero panel.
    .hero.is-download {
        background-image: linear-gradient($download, $download-light);
    }
    
    //Fills up the space.
    .column {
        height: 100%;
        padding: 0.75rem 0.5rem;
    }

    //Makes the buttons inside the columns more rounded and expanded. Avoid aplying to the others.
    .requirements .column .button {
        white-space: normal;
        border-radius: 5px;
        padding: 0.75rem;
        width: 100%;
        height: 100%;
    }

    .has-side-padding {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }

    .has-side-margin {
        margin-left: 1.5rem;
    }

    div .has-top-margin {
        margin-top: 1rem;
    }

    .has-pointer-cursor {
        cursor: pointer;
    }

    .has-arrow-cursor {
        cursor: default;
    }

    div .table-wrapper {
        overflow-x: hidden !important;
    }

    hr {
        margin: 1rem 0;
    }
</style>

<style lang="scss">
    //Removes the hover effect on the detail of the row.
    .table.is-hoverable tbody tr.detail:not(.is-selected):hover {
        background-color: hsl(0, 0, 98%);
    }
</style>